/**
 * Horizontal slider style (default).
 */
.range-bar {
  background-color: #a9acb1;
  border-radius: 15PX;
  display: block;
  height: 1PX;
  position: relative;
  width: 100%;
}
.range-bar-disabled {
  opacity: 0.5;
}
.range-quantity {
  background-color: #04BE02;
  border-radius: 15PX;
  display: block;
  height: 100%;
  width: 0;
}
.range-handle {
  background-color: #fff;
  border-radius: 100%;
  cursor: move;
  height: 10PX;
  left: 0;
  top: -13PX;
  position: absolute;
  width: 10PX;
  -webkit-box-shadow: 0 1PX 3PX rgba(0, 0, 0, 0.4);
  box-shadow: 0 1PX 3PX rgba(0, 0, 0, 0.4);
}
.range-min,
.range-max {
  color: rgba(0, 0, 0, 0);
  font-size: 12PX;
  position: absolute;
  text-align: center;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 8PX;
}
.range-min {
  left: -30PX;
}
.range-max {
  right: -30PX;
}

/**
 * Style for disabling text selection on handle move.
 */
.unselectable {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/**
 * Style for handle cursor on disabled slider.
 */
.range-disabled {
  cursor: default;
